<template>
  <div>
    <table>
    <h3>Danh sách sản phẩm:</h3>
      <tr>
        <th>Name</th>
        <th>Price</th>
        <th>Qty</th>
        <th></th>
      </tr>

      <tr v-for="(product,index) in products" :key="index">
        <td>{{ product.name }}</td>
        <td>{{ product.price | formatMoney }}</td>
        <td><input type="number" name="quantity" v-model="product.qty" min="1"></td>
        <td><button @click="Cart.push({name: product.name, price: product.price, qty: product.qty})">Add to Cart</button></td>
      </tr>      
    </table>
  </div>
  
</template>

<script>
export default {
  props: ['products','Cart'],
  filters: {
    formatMoney(x) {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }
  },
}
</script>


